body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  margin: 0;
  padding: 0; }

.page__wrapper {
  background-color: #fee037;
  border: 2px solid black; }

.shapes__content {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between; }

.triangle__left {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 80px 80px 0 0;
  border-color: black transparent transparent transparent; }

.triangle__righ {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 80px 80px 0;
  border-color: transparent black transparent transparent; }

.header__title {
  text-align: center; }

/* .main__content{} */
/* .header__content{} */
/* filter */
.main__filter {
  text-align: center; }

.poke__filter {
  width: 300px; }

.poke__list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  list-style: none; }

.poke__list--item {
  width: 250px;
  margin: 20px;
  border: 2px solid black;
  background-color: #ebf5f9; }

.item__image {
  background-color: #e6e6e6;
  text-align: center; }

/* .image__style{} */
.id__text {
  width: 60px;
  margin: 0;
  background-color: #bfbebe;
  border: 2px solid #bfbebe; }

.item__name {
  text-transform: capitalize; }

.name__title {
  text-align: left;
  padding-left: 10px;
  margin-bottom: 0; }

.item__types {
  display: -webkit-flex;
  display: flex; }

.types__text {
  border: 1px solid black;
  text-transform: uppercase;
  font-size: 10px;
  border-radius: 5px;
  padding: 5px;
  margin: 10px; }

/* footer */
.circle__left {
  height: 70px;
  width: 130px;
  -webkit-clip-path: circle(67% at 6% 100%);
          clip-path: circle(67% at 6% 100%);
  background: red; }

.circle__righ {
  height: 70px;
  width: 130px;
  -webkit-clip-path: circle(67% at 90% 100%);
          clip-path: circle(67% at 90% 100%);
  background: red; }

